<template>
  <div>
    <div class="title-row">
      <span class="title-text">Block Summary</span>
    </div>
    <div>
      <div class="single-row" v-for="item in keyList" :key="item">
        <div class="left-part">
          <span class="left-text">{{ item }}</span>
        </div>
        <div class="right-part">
          <span class="right-text">
            {{ oriData[item] }}
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    oriData: Object | null
  },
  data() {
    return {
      keyList: []
    };
  },
  watch: {
    oriData: {
      immediate: true,
      deep: true,
      handler: function() {
        if (this.oriData && this.oriData.hash) {
          this.keyList = Object.keys(this.oriData);
        }
      }
    }
  }
};
</script>

<style scoped>
.container {
  width: 100%;
}
.title-row {
}
.title-text {
  color: rgb(53, 63, 82);
  font-family: Inter, Helvetica, sans-serif;
  font-feature-settings: "calt" 0;
  font-weight: 700;
  font-size: 1.5rem;
  text-transform: none;
  font-style: normal;
}
.single-row {
  display: flex;
  padding: 10px 0;
  border-bottom: 1px solid rgb(223, 227, 235);
}
.left-part {
  width: 30%;
}
.right-part {
  width: 70%;
}
.left-text {
  font-weight: 500;
  font-size: 14px;
  text-transform: none;
  font-style: normal;
  color: rgb(103, 113, 133);
  text-overflow: ellipsis;
}
.right-text {
  font-family: Inter, Helvetica, sans-serif;
  font-feature-settings: "calt" 0;
  font-weight: 500;
  font-size: 14px;
  text-transform: none;
  font-style: normal;
  text-overflow: ellipsis;
  color: rgb(53, 63, 82);
}
</style>



