<template>
  <div class="app-container">
    <div class="search">
      <input class="input" type="text" v-model="searchHash" />
      <div class="search-btn">
        <ElButton @click="handleClickSearch">search</ElButton>
      </div>
    </div>
    <template v-if="loading">
      查询中...
    </template>
    <template v-else-if="!!result">
      <MainWindowSummary :oriData="result"/>
      <MainWindowTransactions style="margin-top: 20px;" :list="txList"
    /></template>
    <template v-else>
      查无数据
    </template>
  </div>
</template>

<script>
import MainWindowSummary from "./main-window-summary.vue";
import MainWindowTransactions from "./main-window-transactions.vue";
import { MockData } from "./mock";
import { MessageBox } from "element-ui";
export default {
  components: {
    MainWindowSummary,
    MainWindowTransactions
  },
  data() {
    return {
      searchHash: "",
      result: null,
      txList: [],
      loading: false
    };
  },
  methods: {
    query() {
      let tempHash = this.searchHash;
      return new Promise((resolve, reject) => {
        let xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function() {
          if (xhr.readyState == XMLHttpRequest.DONE) {
            resolve(xhr.response);
          }
        };
        xhr.onerror = function(error) {
          reject(error);
        };
        xhr.open("GET", `https://blockchain.info/rawblock/${tempHash}`, true);
        xhr.send();
      });
    },
    handleClickSearch() {
      if (this.loading) {
        return;
      }
      if (!this.searchHash) {
        MessageBox.alert("请输入要查询的hash值");
        return;
      }
      this.loading = true;
      this.result = null;
      this.txList = [];
      this.query()
        .then(res => {
          let last = JSON.parse(res);
          this.txList = last.tx || [];
          delete last.tx;
          this.result = last;
        })
        .catch(() => {
          this.result = null;
          this.txList = [];
        })
        .finally(() => {
          this.loading = false;
        });
    }
  }
};
</script>

<style scoped>
.app-container {
  max-width: 1200px;
  padding: 20px;
  margin: 0 auto;
}
.search {
  margin-bottom: 20px;
  display: flex;
}
.input {
  display: block;
  height: 2.7rem;
  width: 100%;
  padding: 0px 1rem;
  box-sizing: border-box;
  font-family: Inter;
  font-size: 1rem;
  color: rgb(53, 63, 82);
  border-width: 1px;
  border-color: rgb(223, 227, 235);
  border-style: solid;
  border-radius: 0.255rem;
  background-color: rgb(255, 255, 255);
  background-image: none;
  outline-width: 0px;
  user-select: text;
  transition: box-shadow 0.25s ease-out 0s;
}
.search-btn {
  float: right;
  width: 180px;
  text-align: center;
}
</style>



