<template>
  <div class="container">
    <div class="title">
      <span>Block Transactions</span>
    </div>
    <div>
      <div class="single-block" v-for="item in currList" :key="item.hash">
        <div class="single-row">
          <div class="left-part">
            <div class="left-part-left">
              <span class="left-text">Fee</span>
            </div>
            <div class="left-part-right">
              <span class="right-text">
                {{ item.fee }}
              </span>
            </div>
          </div>
        </div>
        <div class="single-row">
          <div class="left-part">
            <div class="left-part-left">
              <span class="left-text">Hash</span>
            </div>
            <div class="left-part-right">
              <span class="right-text">
                {{ item.hash }}
              </span>
            </div>
          </div>
        </div>
        <div class="single-row">
          <div class="left-part">
            <div class="left-part-left ">
              <span class="left-text display-toggle">From</span>
            </div>
            <div class="left-part-right">
              <div class="litt-item" v-for="(little, index) in item.inputs" :key="'in' + index">
                {{ little.prev_out.addr }}
              </div>
            </div>
          </div>
          <div class="right-part">
            <div class="right-part-left display-toggle-2">
              <span class="el-icon-d-arrow-right text-green"></span>
            </div>
            <div class="right-part-left display-toggle-3">
              <span class="left-text display-toggle">To</span>
            </div>
            <div class="right-part-right">
              <div>
                <div class="litt-item" v-for="(little, index) in item.out" :key="'out' + index">
                  {{ little.addr }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Pagination
      style="margin-top: 30px;"
      background
      layout="prev, pager, next"
      :total="list.length"
      @current-change="handlePageChanged"
      :current-page="page"
    />
  </div>
</template>

<script>
export default {
  props: {
    list: Array
  },
  data() {
    return {
      page: 1,
      pageCount: 10,
      currList: ""
    };
  },
  watch: {
    list: {
      immediate: true,
      handler: function() {
        this.reset();
        this.getCurrPage();
      }
    }
  },
  methods: {
    reset() {
      this.page = 1;
    },
    getCurrPage() {
      this.currList = this.list.slice(
        (this.page - 1) * this.pageCount,
        this.page * this.pageCount
      );
    },
    handlePageChanged(page) {
      this.page = page;
      this.getCurrPage();
    }
  }
};
</script>

<style scoped>
.container {
  width: 100%;
}
.title {
  color: rgb(53, 63, 82);
  font-family: Inter, Helvetica, sans-serif;
  font-feature-settings: "calt" 0;
  font-weight: 700;
  font-size: 1.5rem;
  text-transform: none;
  font-style: normal;
}
.single-block {
  border-bottom: 1px solid rgb(223, 227, 235);
  width: 100%;
  position: relative;
}
.single-row {
  padding: 10px 0;
}
.left-part {
  width: 50%;
  min-height: 30px;
  line-height: 30px;
  display: inline-flex;
}
.right-part {
  width: 48%;
  min-height: 30px;
  line-height: 30px;
  display: inline-flex;
}
.left-part-left {
  width: 50px;
  display: inline-block;
}
.left-part-right {
  width: calc(100% - 60px);
  text-overflow: ellipsis;
  display: inline-block;
}
.right-part-left {
  width: 50px;
  display: inline-block;
  line-height: 30px;
}
.right-part-right {
  width: calc(100% - 60px);
  text-overflow: ellipsis;
  display: inline-block;
  line-height: 30px;
}
.left-text {
  font-weight: 500;
  font-size: 14px;
  text-transform: none;
  font-style: normal;
  color: rgb(103, 113, 133);
}
.right-text {
  font-family: Inter, Helvetica, sans-serif;
  font-feature-settings: "calt" 0;
  font-weight: 500;
  font-size: 14px;
  text-transform: none;
  font-style: normal;
  color: rgb(53, 63, 82);
}
.text-green {
  color: green;
}
.display-toggle {
  visibility: hidden;
}
.display-toggle-2 {
  width: 50px !important;
  overflow: hidden;
}
.display-toggle-3 {
  width: 0px !important;
  overflow: hidden;
}
@media screen and (max-width: 1200px) {
  .left-part {
    width: 100%;
    min-height: 30px;
    line-height: 30px;
    display: flex;
  }
  .right-part {
    width: 100%;
    min-height: 30px;
    line-height: 30px;
    display: flex;
  }
  .display-toggle {
    visibility: visible;
  }
  .display-toggle-2 {
    width: 0px !important;
    overflow: hidden;
  }
  .display-toggle-3 {
    width: 50px !important;
    overflow: hidden;
  }
  .left-text {
    word-break: break-all;
  }
  .right-text {
    word-break: break-all;
  }
  .litt-item {
    word-break: break-all;
    margin-bottom: 10px;
  }
}
</style>



